export const createIdChat = (myId: string | null, userId: string | null) => {
  if (myId && userId) {
    let chatId = "";
    if (myId > userId) {
      chatId = myId + userId;
      return chatId;
    } else {
      chatId = userId + myId;
      return chatId;
    }
  }
  return null
};
