import React from 'react'
import { useDispatch } from 'react-redux'
import { NavLink } from 'react-router-dom'
import { RouteNames } from '../../route/route'
import { setSelectedChat } from '../../store/slice/selectedChatSlice'
import { userType } from '../../types/user'
import { createIdChat } from '../../utils/createIdChat'
import './AsideUserItem.scss'
interface AsideUserItemProps {
  lastMessage?: string | null,
  user: userType,
  chatId: string | null,
}
const AsideUserItem:React.FC<AsideUserItemProps> = ({lastMessage,user,chatId}) => {
  const dispatch = useDispatch()
  const setChat = () => {
   dispatch(setSelectedChat({
      chatId: chatId,
      selectedUser: user,
    }))
  }
  return (
    <NavLink onClick={setChat} to={RouteNames.ChatId + chatId} className='aside__chat'>
    <div className='aside__chat-wrap'>
      <div className='aside__chat-img'>
        <img className='aside__chat-img' src={user.img ? user.img : 'https://static.thenounproject.com/png/2504016-200.png'} alt="userIcon" />
      </div>
      <div className='aside__chat-text'>
        <div className='aside__chat-text-name'>
          {user.name}
        </div>
        <div className='aside__chat-text-last-message'>
          {lastMessage ? lastMessage : 'немає повідомлень'}
        </div>
      </div>
    </div>
  </NavLink>
  )
}

export default AsideUserItem